/**
 * 1.
 * @param {Number} value
 */
function isEleven(value, fn) {
    const reg = /\d{11}/g
    const result = {
        hint: ' , ',
        isBlock: false,
        isError: false,
        isYes: false
    }
    if (value.length !== 11 && !reg.test(value)) {
        result.hint = '11'
        result.isBlock = true
        result.isError = true
    } else {
        result.isYes = true
    }
    fn(result)
}

/**
 * 
 * @param {Number} value
 * @param {*} fn
 */
function verifyCode(value, fn) {
    const reg = /\d{6}/g
    const result = {
        hint: null,
        isBlock: false,
        isYes: false
    }
    if (value.length !== 6 && !reg.test(value)) {
        if (value === '') {
            result.hint = ''
        } else {
            result.hint = ''
        }
        result.isBlock = true
    } else {
        result.isYes = true
    }
    fn(result)
}

/**
 *  
 * @param {security} value
 * @param {*} fn
 * .vue
 */
function createPass(value, fn) {
    const reg = /^[\d|A-z_*]{8,23}$/g
    const result = {
        isSpanBlock: true,
        status: null,
        isYes: null,
        isBlock: null,
        strengthLevel: 0
    }
    if (!reg.test(value)) {
        if (value === '') {
            result.status = ''
        } else if (value.length < 8) {
            result.status = '8'
        } else if (value.length > 23) {
            result.status = '23'
        } else {
            result.status = ''
        }
    } else {
        if (
            /[a-z|A-Z]/g.test(value) &&
            /\d/g.test(value) &&
            value.length > 10
        ) {
            result.strengthLevel = 3
        } else if (/[a-z|A-Z_*]/g.test(value) && /\d/g.test(value)) {
            result.strengthLevel = 2
        } else {
            result.strengthLevel = 1
        }
        result.isYes = true
        result.isSpanBlock = false
    }

    fn(result)
}

/**
 *  
 * @param {security} value
 * @param {*} fn
 */
function affirmPass(value, createValue, fn) {
    const result = {
        isYes: null,
        hint: null,
        isBlock: null
    }
    if (value === createValue && value !== '') {
        result.isYes = true
    } else {
        if (value === '') {
            result.hint = ''
        } else {
            result.hint = ''
        }
        result.isBlock = true
    }
    fn(result)
}

/**
 * 
 * @param {this} instance
 * @param {*} e
 */
function focusChange(instance, e) {
    const inputType = e.target.name
    if (inputType === 'iphone') {
        instance.iphoneInpHint.isBlock = false
    } else if (inputType === 'dynamicPass') {
        instance.dynamicInpHint.isBlock = false
    } else if (inputType === 'createPass') {
        instance.createInpHint.isBlock = false
    } else if (inputType === 'affirmPass') {
        instance.affirmInpHint.isBlock = false
    }
}
export default {
    isEleven,
    focusChange,
    verifyCode,
    createPass,
    affirmPass
}
